import React from "react";
import { appendUserData } from "../../redux/actions/controlUser";
import { useDispatch } from "react-redux";
import { Button, Form, Input, Select, message } from "antd";
import { useNavigate } from "react-router-dom";

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const validateMessages = {
  required: "",
};
const cityOption = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];
export default function UserForm() {
  const dispatch = useDispatch();
  const navigate = useNavigate()
  const [loginApi, contextLogin] = message.useMessage();
  const success = () => {
    loginApi.open({
      type: "success",
      content: "",
    });
  };
  const getNowTime = () => {
    const today = new Date();
    const year = today.getFullYear().toString();
    const month = (today.getMonth() + 1).toString();
    const day = today.getDate().toString();
    const result = year + "/" + month + "/" + day;
    return result;
  };
  const onFinish = (values) => {
    const { user } = values;
    const data = {
      title: user.title,
      city: user.city,
      payRnge: user.pay,
      fullJob: user.fullJob,
      workFromHome: user.remote,
      workTime: user.WorkingHours,
      relaxTime: user.vocation,
      toWorkTime: user.workingDay,
      information: user.introduction,
      id: Math.random(Date.now()).toString(2),
      phoneNumber: user.phoneNumber,
      emailAddress: user.emailAddress,
      updateTime: getNowTime(),
    };
    dispatch(appendUserData(data));
    success();
    setTimeout(() => {
      navigate("/show");
    }, 3000);
  };
  return (
    <>
      {contextLogin}
      <Form
        {...layout}
        name="nest-messages"
        onFinish={onFinish}
        validateMessages={validateMessages}
      >
        <Form.Item
          name={["user", "title"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["user", "emailAddress"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["user", "phoneNumber"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["user", "city"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            {cityOption.map((item, index) => {
              return (
                <Select.Option value={item} key={index}>
                  {item}
                </Select.Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          name={["user", "pay"]}
          label=""
          rules={[{ required: true }]}
        >
          <Input placeholder="35,500~70,000" />
        </Form.Item>
        <Form.Item
          name={["user", "fullJob"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["user", "remote"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["user", "WorkingHours"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=" - "> - </Select.Option>
            <Select.Option value=" - "> - </Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["user", "vocation"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["user", "workingDay"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item name={["user", "introduction"]} label="">
          <Input.TextArea rows={7} />
        </Form.Item>

        <Form.Item
          wrapperCol={{
            ...layout.wrapperCol,
            offset: 8,
          }}
        >
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </>
  );
}
