import React from "react";
import { useDispatch } from "react-redux";
import { appendCompanyData } from "../../redux/actions/controlCompany";
import { useNavigate } from "react-router-dom";
import { Button, Form, Input, Select,message } from "antd";

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const validateMessages = {
  required: "",
};
const cityOption = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];
const seniority = ["", "1", "2", "3", "4", "5"];

export default function CompanyForm() {
  const navigate = useNavigate()
  const [loginApi, contextLogin] = message.useMessage();
  const success = () => {
    loginApi.open({
      type: "success",
      content: "",
    });
  };
  const getNowTime = () => {
    const today = new Date();
    const year = today.getFullYear().toString();
    const month = (today.getMonth() + 1).toString();
    const day = today.getDate().toString();
    const result = year + "/" + month + "/" + day;
    return result;
  };
  const dispatch = useDispatch();
  const onFinish = (values) => {
    const { company } = values;
    const data = {
      city: company.city,
      companyname: company.companyname,
      pay: company.pay,
      information: company.introduction,
      id: Math.random(Date.now()).toString(2),
      detail: [
        company.fullJob,
        company.address,
        company.remote,
        company.responsibility,
        company.expatriate,
        company.WorkingHours,
        company.vocation,
        company.workingDay,
        company.recruitLimit,
      ],
      title: company.title,
      seniority: company.seniority,
      update: getNowTime(),
      phoneNumber: company.phoneNumber,
      emailAddress: company.emailAddress,
    };
    dispatch(appendCompanyData(data));
    success()
    setTimeout(() => {
      navigate('/show')
    }, 3000);
  };
  return (
    <>
      {contextLogin}
      <Form
        {...layout}
        name="nest-messages"
        onFinish={onFinish}
        validateMessages={validateMessages}
      >
        <Form.Item
          name={["company", "companyname"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "title"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "emailAddress"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "phoneNumber"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "seniority"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select>
            {seniority.map((item, index) => {
              return (
                <Select.Option value={item} key={index}>
                  {item}
                </Select.Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          name={["company", "city"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            {cityOption.map((item, index) => {
              return (
                <Select.Option value={item} key={index}>
                  {item}
                </Select.Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          name={["company", "pay"]}
          label=""
          rules={[{ required: true }]}
        >
          <Input placeholder="35,500~70,000" />
        </Form.Item>
        <Form.Item
          name={["company", "fullJob"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["company", "address"]}
          label=""
          rules={[{ required: true }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "remote"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["company", "responsibility"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["company", "expatriate"]}
          label=""
          rules={[{ required: true }]}
        >
          <Select>
            <Select.Option value=""></Select.Option>
            <Select.Option value=""></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name={["company", "WorkingHours"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "vocation"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "workingDay"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name={["company", "recruitLimit"]}
          label=""
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item name={["company", "introduction"]} label="">
          <Input.TextArea rows={10} />
        </Form.Item>

        <Form.Item
          wrapperCol={{
            ...layout.wrapperCol,
            offset: 8,
          }}
        >
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </>
  );
}
